/* start  css featured-course */
.course-item .course-img-wrapper {
  overflow: hidden;
}
.course-item .course-img-wrapper .course-img {
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}
.course-item:hover .course-img-wrapper .course-img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.featured-course-title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  min-height: 60px;
}
.name-pt {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
