/* -----start css button ------ */
.theme-btn {
  cursor: pointer;
  display: inline-block;
  transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
}
.btn-style-three {
  position: relative;
  display: inline-block;
  font-size: 15px;
  line-height: 30px;
  color: #222222;
  padding: 10px 36px;
  font-weight: 600;
  overflow: hidden;
  border-radius: 0px;
  text-transform: capitalize;
  border: 2px solid #959595;
  font-family: "Montserrat", sans-serif;
  transition: all 600ms ease;
}
.btn-style-three .txt {
  position: relative;
  z-index: 1;
}
.btn-style-three:hover {
  color: #ffffff;
  border-color: #ff0000;
}
.btn-style-three:before {
  position: absolute;
  content: "";
  top: -1px;
  bottom: 0px;
  left: 0px;
  right: -50px;
  border-bottom: 55px solid #ff0000;
  border-right: 30px solid transparent;
  transition: all 600ms ease;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}
.btn-style-three:after {
  position: absolute;
  content: "";
  top: -1px;
  right: 0px;
  bottom: 0px;
  left: -50px;
  border-left: 30px solid transparent;
  border-bottom: 55px solid #ff0000;
  transition: all 600ms ease;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}
.btn-style-three:hover::before {
  -webkit-transform: translateX(-40%);
  transform: translateX(-40%);
}
.btn-style-three:hover::after {
  -webkit-transform: translateX(40%);
  transform: translateX(40%);
}
.btn-three-outer {
  position: relative;
  padding-left: 7px;
  padding-bottom: 7px;
  padding-right: 3px;
  padding-top: 6px;
  z-index: 1;
  display: inline-block;
  transition: all 600ms ease;
}
.btn-three-outer:before {
  position: absolute;
  content: "";
  left: 0px;
  bottom: 2px;
  width: 44px;
  height: 44px;
  z-index: -1;
  border-left: 2px solid #ff0000;
  border-bottom: 2px solid #ff0000;
  transition: all 600ms ease;
}
.btn-three-outer:after {
  position: absolute;
  content: "";
  right: 3px;
  top: 9px;
  width: 44px;
  height: 44px;
  opacity: 0;
  z-index: -1;
  border-right: 2px solid #ff0000;
  border-top: 2px solid #ff0000;
  transition: all 600ms ease;
}
.btn-three-outer:hover::after {
  opacity: 1;
  width: 100%;
  height: 100%;
}
.btn-three-outer:hover::before {
  opacity: 0;
}
.btn-three-outer:hover .btn-style-three {
  color: #ffffff;
  -webkit-transform: translate(-7px, 10px);
          transform: translate(-7px, 10px);
}
/* ----- end css button------ */
.float_up_down {
  -webkit-animation-name: float_up_down;
          animation-name: float_up_down;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes float_up_down {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }

  50% {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
    transform: translateY(30px);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes float_up_down {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }

  50% {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
    transform: translateY(30px);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
}

